.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

button {
  display: block;
  background: transparent;
  border: none;
}

.btn {
  width: 180px;
  height: 50px;
  background-color: #ff1a2c;
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-transform: uppercase;
  text-align: center;
  margin: 0 auto;
  transition: background-color .4s ease-in-out;
}

.btn:hover {
  cursor: pointer;
  background-color: #191919;
}

.title {
  color: #333333;
  font-family: 'Fjalla One', sans-serif;
  font-size: 44px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-transform: uppercase;
  text-align: center;
}

.description {
  color: #ff1a2c;
  font-family: 'Great Vibes', cursive;
  font-size: 30px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-align: center;
  margin: 0 auto;
}

.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 111;
  display: flex;
}

.nav {
  padding-top: 52px;
}

.nav__menu {
  display: flex;
  align-items: center;
  margin: 0;
  padding-left: 0;
  font-family: "Open Sans", sans-serif;
}

.nav__menu--end {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.nav__menu--end .nav__link:first-child {
  width: 50%;
}

.nav__link-item {
  color: #ffffff;
  font-family: 'Fjalla One', sans-serif;
  font-size: 20px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  position: relative;
  transition: all ease-in-out 0.3s;
}

.nav__link-item:hover:after {
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #ff1a2c;
  margin-top: 13px;
}

body {
  font-family: "Open Sans", sans-serif;
  position: relative;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.inner {
  height: 780px;
  background: url("../img/top-bg.png") center no-repeat;
  background-size: cover;
  position: relative;
}

.inner__contact-btn {
  position: absolute;
  bottom: 150px;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 180px;
  height: 50px;
  background-color: #ff1a2c;
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-transform: uppercase;
  text-align: center;
}

.about {
  padding: 70px 0;
}

.about__description {
  margin: 0 auto 14px;
}

.about__title {
  margin-bottom: 50px;
}

.about__txt {
  color: #22191a;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  margin-bottom: 35px;
}

.about__image-item {
  max-width: 45%;
}

.classes {
  padding: 70px 0;
  background-color: #22191a;
}

.classes__description {
  margin-bottom: 15px;
}

.classes__title {
  color: white;
  margin-bottom: 45px;
}

.classes__subtitle {
  max-width: 933px;
  color: #ffffff;
  font-family: "Open Sans", SansSerif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-align: center;
}

.classes__content {
  margin-bottom: 40px;
}

.classes__btn {
  margin-top: 50px;
}

.slider-button-prev {
  cursor: pointer;
}

.slider-button-next {
  cursor: pointer;
  margin-left: auto;
}

.swiper-wrapper {
  margin-bottom: 45px;
}

.swiper-pagination-bullet {
  width: 16px;
  height: 16px;
  border: 1px solid #ff1a2c;
  border-radius: 0;
}

.swiper-pagination-bullet-active {
  position: relative;
  background: none;
}

.swiper-pagination-bullet-active:after {
  position: absolute;
  content: '';
  width: 50%;
  height: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  background: #ff1a2c;
}

.training {
  padding: 70px 0;
}

.training__description {
  margin-bottom: 15px;
}

.training__title {
  margin-bottom: 50px;
}

.training__image {
  height: 100%;
}

.training__info {
  min-height: 420px;
  max-width: 90%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.training__txt {
  color: #22191a;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-align: center;
  margin: 0 auto;
}

.training__number {
  font-family: 'Fjalla One', sans-serif;
  font-size: 30px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-align: center;
  color: #22191a;
}

.training__number a {
  text-decoration: none;
  color: #ff1a2c;
}

.tasting {
  padding: 70px 0;
  background-color: #22191a;
}

.tasting__description {
  margin-bottom: 15px;
}

.tasting__title {
  color: #ffffff;
  margin-bottom: 53px;
}

.tasting__content {
  flex-wrap: wrap;
  justify-content: space-around;
  text-align: center;
}

.tasting__item-image {
  position: relative;
  max-height: 350px;
}

.tasting__item-image:hover .tasting__item-hover {
  display: block;
}

.tasting__item {
  margin-bottom: 58px;
}

.tasting__item img {
  width: 100%;
  object-fit: contain;
}

.tasting__item-hover {
  display: none;
  z-index: 10;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(25, 25, 25, 0.5);
}

.tasting__item-btn {
  background-color: #ff1a2c;
  position: absolute;
  z-index: 222222222222;
  opacity: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.tasting__item-title {
  color: #ffffff;
  font-family: "Fjalla One", sans-serif;
  font-size: 22px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 32px;
  text-transform: uppercase;
  margin-top: 16px;
}

.clients {
  padding: 70px 0;
  background: url(../img/clienst-bg.jpg) center no-repeat;
  font-family: "Open Sans", sans-serif;
  background-size: cover;
}

.clients__description {
  margin-bottom: 15px;
}

.clients__title {
  color: white;
  margin-bottom: 53px;
}

.clients__slider:before {
  content: '';
  display: block;
  width: 23px;
  height: 19px;
  background: url("../img/clients-icon.png") center no-repeat;
  margin: 0 auto 20px;
}

.clients__slide {
  text-align: center;
}

.clients__txt {
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-align: left;
  margin-bottom: 25px;
}

.clients__author {
  color: #ffffff;
  font-size: 16px;
}

.client-container {
  width: 600px;
  height: 300px;
}

.swiper-clients-container {
  overflow-x: hidden;
}

.footer {
  min-height: 160px;
  background-color: #22191a;
  padding: 10px 0;
}

.footer__content {
  align-items: center;
}

.footer__social-title {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-transform: uppercase;
}

.footer__social-link {
  width: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}

.footer .nav {
  text-align: center;
  padding: 0;
}

.footer-nav__link-item:hover:after {
  position: relative;
}

.footer__social-link__item {
  padding: 10px;
  border: 1px solid #ff1a2c;
  transition: background-color ease-in-out .4s;
}

.footer__social-link__item:hover {
  background-color: #ff1a2c;
}

.footer__social-link__item:hover svg {
  fill: #000000;
}

.wine__popup {
  background: rgba(0, 0, 0, 0.7);
  cursor: pointer;
  display: none;
  height: 100%;
  position: fixed;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  z-index: 10000;
}

.wine__popup-wrap {
  position: relative;
  top: 3%;
  padding: 3%;
  width: 100%;
  background-color: #ffffff;
}

.wine__popup-title {
  font-size: 22px;
  text-align: left;
  margin-bottom: 39px;
}

.wine__popup-txt {
  color: #22191a;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  letter-spacing: normal;
  line-height: 28px;
  text-align: left;
  margin-bottom: 40px;
}

.wine__close-popup {
  position: absolute;
  font-size: 20px;
  top: 15px;
  right: 25px;
  padding: 5px;
}

@media screen and (max-width: 1220px) {
  .footer__content {
    justify-content: space-around;
  }
  .footer .nav {
    order: -1;
    margin-bottom: 25px;
  }
  .footer__logo img {
    width: 90%;
  }
  .footer-nav__link-item {
    font-size: 14px;
  }
}

@media screen and (max-width: 992px) {
  .about__txt {
    text-align: center;
  }
  .about__btn {
    margin-bottom: 35px;
  }
  .wine__close-popup {
    top: 0;
  }
  .footer__logo img {
    width: 75%;
  }
  .footer__social-title {
    font-size: 12px;
  }
}

@media screen and (max-width: 810px) {
  .wine__popup-title {
    text-align: center;
    font-size: 18px;
    margin-bottom: 20px;
  }
  .wine__popup-txt {
    text-align: center;
    font-size: 14px;
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 798px) {
  .header__logo {
    display: none;
  }
  .inner {
    height: 100vh;
  }
  .footer__logo {
    margin-left: 30px;
  }
  .footer nav {
    order: -1;
  }
}

@media screen and (max-width: 767px) {
  .inner__contact-btn {
    bottom: 95px;
  }
  .training__description {
    margin-top: 30px;
  }
  .training__title {
    margin-bottom: 30px;
  }
  .wine__popup-txt:last-child {
    display: none;
  }
}

@media screen and (max-width: 590px) {
  .inner__contact-btn {
    bottom: 70px;
  }
  .tasting__item-image img {
    width: auto;
  }
  .wine__popup-wrap {
    padding: 10%;
  }
  .footer__logo img {
    width: 50%;
  }
  .footer__social-title {
    font-size: 10px;
  }
}

@media screen and (max-width: 562px) {
  .nav__link-item {
    font-size: 10px;
  }
  .training__number {
    margin-top: 20px;
  }
  .tasting__item {
    margin-bottom: 38px;
  }
  .tasting__item-title {
    margin: 15px auto 0;
    max-width: 70%;
  }
}
